const pageQuery = `{
  pages: allMdx {
    edges {
      node {
        objectID: id
	internal {
	  contentDigest
	}
        fields {
          slug
        }
        frontmatter {
          title
          metaDescription 
        }
        excerpt(pruneLength: 50000)
      }
    }
  }
}`;

const flatten = arr =>
  arr.map(({ node: { frontmatter, fields, ...rest } }) => ({
    ...frontmatter,
    ...fields,
    ...rest,
  }));

const settings = { attributesToSnippet: [`excerpt:20`] };

const indexName = header.search ? header.search.indexName : '';

export const queries = [
  {
    query: pageQuery,
    transformer: ({ data }) => flatten(data.pages.edges),
    indexName: `${indexName}`,
    settings,
  },
];


